<template>
  <div v-if="availableParts" class="content">
    <div class="preview">
      <CollapsibleSection>
      <div class="preview-content">
        <div class="top-row">
          <img :src="selectedRobot.head.src"/>
        </div>
        <div class="middle-row">
          <img :src="selectedRobot.leftArm.src" class="rotate-left"/>
          <img :src="selectedRobot.torso.src"/>
          <img :src="selectedRobot.rightArm.src" class="rotate-right"/>
        </div>
        <div class="bottom-row">
          <img :src="selectedRobot.base.src"/>
        </div>
      </div>
      </CollapsibleSection>
      <button class="add-to-cart" @click="addToCart()">Add to cart</button>
    </div>
    <div class="top-row">
        <!-- <div class="robot-name">
          {{ selectedRobot.head.title }}
          <span v-if="selectedRobot.head.onSale" class="sale">Sale!</span> -->
          <!-- v-if="" creates a div. use this when there is low cost to re-rendering  -->
          <!-- v-show="" uses display: none /block. use this when it costs much to do v-if;
           example: the v-if creates multiple child divs  -->
      <PartSelector :parts="availableParts.heads" position="top"
       @partSelected="part => selectedRobot.head=part"/>
    </div>
    <div class="middle-row">
      <PartSelector :parts="availableParts.arms" position="left"
      @partSelected="part => selectedRobot.leftArm=part"/>
      <PartSelector :parts="availableParts.torsos" position="center"
      @partSelected="part => selectedRobot.torso=part"/>
      <PartSelector :parts="availableParts.arms" position="right"
      @partSelected="part => selectedRobot.rightArm=part"/>
    </div>
    <div class="bottom-row">
      <PartSelector :parts="availableParts.bases" position="bottom"
      @partSelected="part => selectedRobot.base=part"/>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex';
// add : , mapMutations above to add mapMutations from vuex
import createdHookMixin from './created-hook-mixin';
import PartSelector from './PartSelector.vue';
import CollapsibleSection from './CollapsibleSection.vue';

export default {
  name: 'RobotBuilder',
  created() {
    this.getParts();
  },
  components: { PartSelector, CollapsibleSection },
  data() {
    return {
      cart: [],
      selectedRobot: {
        head: {},
        torso: {},
        base: {},
        leftArm: {},
        rightArm: {},
      },
    };
  },
  mixins: [createdHookMixin],
  computed: {
    availableParts() {
      return this.$store.state.robots.parts;
    },
    saleBorderClass() {
      return this.selectedRobot.head.onSale ? 'sale-border' : '';
    },
  },
  methods: {
    ...mapActions('robots', ['getParts', 'addRobotToCart']),
    // ...mapMutations('robots', ['someMutations']),
    addToCart() {
      const robot = this.selectedRobot;
      const cost = robot.head.cost + robot.leftArm.cost + robot.torso.cost + robot.base.cost
      + robot.rightArm.cost;
      this.addRobotToCart({ ...robot, cost }).then(() => this.$router.push('/cart'));
      this.cart.push({ ...robot, cost });
    },
  },
};
</script>
